<template >
  <div class="wrapper">
    <FlashMessage class="flashmessage"></FlashMessage>
      <div class="content-container">
        <div class="">
          <div class="container-fluid flex-grow-0 bg-gradient p-4 mb-5">
            <div class="row">
              <div class="col-12">
                <h5>Select Event</h5>
                <p class="mb-0">Below are the events for which you have been approved. To begin scanning, tap the event you are currently working.</p>
              </div>
            </div>
          </div>
          <div class="container-fluid flex-grow-1">
          <div class="row">
            <div class="col-12">
               <!-- <form @submit.prevent="login" id="login-form"> -->
                  <!-- <b-input-group size="lg" class="mt" prepend="@"> -->
                    <!-- <b-form-input type="text"
                    placeholder="Registeration Number"
                    name="register_number"
                    id="register_number"
                    v-model="form.register_number"></b-form-input> -->
                    <ul class="list-group mb-3">
                      <!-- <a  > -->
                      <li class="list-group-item event-list py-5" v-on:click="goToSearch(event.id, event.additional_information,event.event_name)"  v-bind:class="{ 'active' : isSelected(event.id) }" v-for="event in events">
                        {{ event.event_name }}
                          <i class="fas fa-angle-right icon"></i>
                      </li>
                      <!-- </a> -->
                    </ul>
                  <!-- </ul list-group> -->
               <!-- </form> -->
            </div>
          </div>
        </div>
      </div>
  <!-- Bottom Navigation -->
  <ul class="nav nav-bottom fixed-bottom nav-pills nav-justified">
    <li class="nav-item">
      <router-link class="nav-link disabled" to="/events">
        <i class="fas fa-search icon"></i>
        <span class="text">Search</span>
      </router-link>
    </li>
    <li class="nav-item">
          <span class="nav-link" id="infoMenu" @click="goToInfo">
            <i class="fas fa-info-circle icon"></i>
          <span class="text">Info</span>
          </span>
    </li>
    <li class="nav-item">
      <a class="nav-link" data-toggle="modal" data-target="#logoutModal" href="#logoutModal">
        <i class="fas fa-sign-out-alt icon"></i>
      <span class="text">Logout</span>
      </a>
    </li>
  </ul>
  <!-- /Bottom Navigation End -->
  </div>
  <!-- Modal Logout -->
    <div class="modal fade" id="logoutModal" tabindex="-1" ref="logoutmodal" role="dialog" aria-labelledby="logoutModalLabel" aria-hidden="false">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-body">
            <h5 class="modal-title" id="logoutModalLabel">Log Out</h5>
            <p>You are logging out of VAPP.</p>
            <div class="form">
              <div class="form-group">
                <div class="row justify-content-center">
                  <div class="col-11">
                    <button type="button" class="btn btn-block btn-primary" @click.stop="logout" data-dismiss="modal">Log Out</button>
                  </div>
                </div>
              </div>
              <div class="form-group">
                <div class="row justify-content-center">
                  <div class="col-11">
                    <button type="button" class="btn btn-block btn-secondary" data-dismiss="modal">Cancel</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- /Modal Logout -->
  </div>
</template>
<script type="text/javascript">
import Auth from "../assets/store/auth";
import { get, post } from "../assets/helpers/api";
import { faStar, faMobile, faSquare } from '@fortawesome/free-solid-svg-icons';
import { faCaretSquareRight } from '@fortawesome/free-solid-svg-icons';
import Cache from "@aws-amplify/cache";
var today = new Date();
const expiration = new Date(today.getFullYear(), (today.getMonth()), today.getDate() + 1);
export default {
  data() {
    return {
      form: {
        email: "",
        password: ""
      },
      selected: 0,
      dis: true,
      isLoading: false,
      fullPage: true,
      searchvapp: false,
      myIcon: faStar,
      myIcon1: faMobile,
      myIcon2: faSquare,
      myIcon3: faCaretSquareRight,
      error: {},
      name: "",
      vapps: [],
      vals: "",
      vappdetails: [],
      events: [],
      isProcessing: false,
    };
  },
  created() {
    this.isProcessing = true;
    this.error = {};
      if (Auth.state.api_token && Auth.state.user_id) {
          if(navigator.onLine){
              get(`/api/vapp/events`)
                  .then(response => {
                      this.events = response.data;
                      this.dis = true;
                      localStorage.setItem('events', JSON.stringify(response.data))
                  })
                  .catch(err => {
                      this.flashMessage.error({
                          title: "Error",
                          message: "Something went wront please try again later",
                          time: 5000,
                          flashMessageStyle: {
                              backgroundColor: "#C91F40"
                          }
                      });
                      //this.$router.push({ path: "/#/events" });
                  });

              //  cache all vapps details
              get(`/api/vapp/vappalldetails`)
                  .then(response => {
                      localStorage.setItem('vappalldetails', JSON.stringify(response.data))
                  })
                  .catch(err => {
                      this.flashMessage.error({
                          title: "Error",
                          message: "Something went wront please try again later",
                          time: 5000,
                          flashMessageStyle: {
                              backgroundColor: "#C91F40"
                          }
                      });
                  });
              this.isProcessing = false;



          } else {
              if (localStorage.getItem('events')) {
                  this.events = JSON.parse(localStorage.getItem('events'));
              }
          }

      } else {
        console.log("sorry here");
        // Auth.set("", "");
        this.$router.push({ path: "/login" });
      }

      this.runBackgroundTasks()
  },

  methods: {
    isSelected: function (i) {
      //this.dis = false;
      return i === this.selected
    },
    search(input) {
      if (input.length < 1) { return [] }
      return this.vehicle_index_number.filter(vin => {
        return vin.toLowerCase()
          .startsWith(input.toLowerCase())
      })
    },
      runBackgroundTasks(){
          if(navigator.onLine) {
              //check if vapps exist in Cache memory
              if (localStorage.getItem("backgroundTasks")) {
                  let backgroundTasks = JSON.parse(localStorage.getItem("backgroundTasks"));
                  if(backgroundTasks.length > 0){
                      backgroundTasks.forEach((item, key) => {
                          if(item != null){
                              get("/api/vapp/vapp_compound_status/"+backgroundTasks[key].event_id+"/"+backgroundTasks[key].compound_id+"/"+backgroundTasks[key].vapp_id+"/"+backgroundTasks[key].body+"/"+backgroundTasks[key].vapp_compound_status)
                                  .then(response => {
                                      if(response.data.status == 200 || response.data.status == 422){
                                          backgroundTasks[key] = null
                                          localStorage.setItem('backgroundTasks', JSON.stringify(backgroundTasks))
                                      }
                                  })
                                  .catch(err => {

                                  });
                          }
                      })
                  }
              }

              if (localStorage.getItem("backgroundConflictTasks")) {
                  let backgroundTasks = JSON.parse(localStorage.getItem("backgroundConflictTasks"));
                  if(backgroundTasks.length > 0){
                      backgroundTasks.forEach((item, key) => {
                          if(item != null && backgroundTasks[key].status == 'N'){
                              get("/api/vapp/reportconflict/"+backgroundTasks[key].event_id+"/"+backgroundTasks[key].compound_id+"/"+backgroundTasks[key].vapp_id+"/"+backgroundTasks[key].body)
                                  .then(response => {
                                      if(response.data.status == 200 || response.data.status == 422){
                                          backgroundTasks[key].status = 'Y'
                                          localStorage.setItem('backgroundConflictTasks', JSON.stringify(backgroundTasks))
                                      }
                                  })
                                  .catch(err => {

                                  });
                          }
                      })
                  }
              }

          }
      },
    getvapp() {
      this.isProcessing = true;
      this.error = {};
      get("/api/vapp/vappdetails/"+this.$refs.vappsearch.value)
        .then(response => {
          this.vappdetails = response.data;
          this.searchvapp = true;
          this.isProcessing = false;
        })
        .catch(err => {
          if (err.code === 422 && err.response.data) {
            this.error = err.response.data.error;

            this.flashMessage.error({
              title: "Error",
              message: err.response.data.error,
              time: 5000,
              flashMessageStyle: {
                backgroundColor: "#43d787"
              }
            });
           }
          this.isProcessing = false;
        });
    },
      logout() {
        console.log("button clicked")
      $("[data-dismiss=modal]").trigger({ type: "click" });
      Auth.set("", "", "");
      //this.$refs['logoutmodal'].modal('hide');
      $("[data-dismiss=modal]").trigger({ type: "click" });
      get("/api/logout")
        .then(response => {
          if (response.data.logged_out) {
            Auth.set("", "", "");
            Auth.remove();
            this.$router.push({ path: "/login" });
          }
        })
        .catch(err => {
          Auth.set("", "", "");
          Auth.remove();
          this.$router.push({ path: "/login" });
        });
    },
      goToSearch(id, info,event_name){
          this.selected = id
          this.dis = false
          this.$router.push({ name: 'compounds', params: { event_id: id, additionalInfo : info ,event_name : event_name} })
      },
      goToInfo(){
          this.$router.push({ name: "info" ,params: {  disableReport : true }})
      },
  }
};
</script>

