<template >
  <div class="wrapper">
    <div class="content-container">
  
 <b-modal ref="confirm-modal" hide-footer title="Using Component Methods">
      <div class="d-block text-center">
        <h3>You have already entered this conflict do you want to continue?</h3>
      </div>
      <b-button class="mt-3 btn btn-primary btn-block" block @click="submitForce">Confirm</b-button>
      <b-button class="mt-2 btn btn-primary btn-block"  block @click="hideConfirmModal">Cancel</b-button>
    </b-modal>

      <b-modal
        id="modal-prevent-closing"
        ref="modal"
        @show="resetModal"
        @hidden="resetModal"
        @ok="handleOk"
      >
        <h5 class="modal-title" id="confirmModalLabel">Confirm Override</h5>
        <form ref="form" @submit.stop.prevent="handleSubmit">
          <b-form-group
            :state="nameState"
            label="Please provide a brief explanation of the conflict, and the action taken"
            label-for="body-input"
            invalid-feedback="body is required"
          >
            <b-form-textarea
              id="textarea-rows"
              v-model="body"
              :state="nameState"
              required
              class="mt-3"
              rows="8"
            ></b-form-textarea>
          </b-form-group>
          <div class="form-group">
            <div class="row justify-content-center">
              <div class="col-11">
                <b-button type="submit" variant="primary" block>SAVE AND CONTINUE</b-button>
              </div>
            </div>
          </div>
        </form>
      </b-modal>
        <div class="container-fluid d-flex h-100 flex-column pt-0 pb-3">

          <div class="row flex-grow-1 pt-5">
            <div class="col-12">
              <h5>VAPP EXPIRED</h5>
              <p v-html="additionalInfo"></p>
            </div>
          </div>

          <b-row class="justify-content-center">
            <b-col class="col-10"><b-button class="btn btn-block btn-light" v-b-modal.modal-prevent-closing v-if="disableReportBtn" disabled>CONFIRM OVERRIDE</b-button><b-button class="btn btn-block btn-light" v-b-modal.modal-prevent-closing v-else="">CONFIRM OVERRIDE</b-button></b-col>
            <b-col class="col-10 mt-3" v-if="online"><b-button class="btn btn-block btn-light" v-on:click="runBackgroundTasks" id="syncBtn">Sync</b-button></b-col>
          </b-row>

          <div class="row mt-2">
            <FlashMessage class="flashmessage"></FlashMessage>
            <b-alert show variant="danger" v-if="vappnotfound === true" class="mb-0" style="width: 100% !important;">
              <h4 class="alert-heading">{{ vappinfo }}</h4>
              <h6>
                {{ vappsearchdetail }}
              </h6>
            </b-alert>

            <b-alert show variant="danger" v-if="reportconflictfound === true" class="mb-0" style="width: 100% !important;">
              <h6>
                {{ vappinfo }}
              </h6>
            </b-alert>

            <b-alert show variant="success" v-if="reportconflictfoundgreen === true" class="mb-0" style="width: 100% !important;">
              <h6>
                {{ vappinfo }}
              </h6>
            </b-alert>

            
          </div>

        </div>
      <!-- Bottom Navigation -->
      <ul class="nav nav-bottom fixed-bottom nav-pills nav-justified">
        <li class="nav-item">
          <a class="nav-link" href="#" v-on:click="goToEvents">
            <i class="fas fa-search icon"></i>
            <span class="text">Search</span>
          </a>
        </li>
        <li class="nav-item">
          <span class="nav-link" to="/#/info">
            <i class="fas fa-info-circle icon"></i>
            <span class="text">Info</span>
          </span>
        </li>
        <li class="nav-item">
          <a class="nav-link" data-toggle="modal" data-target="#logoutModal" href="#logoutModal">
            <i class="fas fa-sign-out-alt icon"></i>
            <span class="text">Logout</span>
          </a>
        </li>
      </ul>
      <!-- /Bottom Navigation -->
    </div>
    <!-- Modal Logout View -->
    <div class="modal fade" id="logoutModal" tabindex="-1" ref="logoutmodal" role="dialog" aria-labelledby="logoutModalLabel" aria-hidden="false">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-body">
            <h5 class="modal-title" id="logoutModalLabel">Log Out</h5>
            <p>You are logging out of VAPP.</p>
            <div class="form">
              <div class="form-group">
                <div class="row justify-content-center">
                  <div class="col-11">
                    <button type="button" class="btn btn-block btn-primary" @click.stop="logout" data-dismiss="modal">Log Out</button>
                  </div>
                </div>
              </div>
              <div class="form-group">
                <div class="row justify-content-center">
                  <div class="col-11">
                    <button type="button" class="btn btn-block btn-secondary" data-dismiss="modal">Cancel</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- /Modal Logout -->
  </div>
</template>
<script type="text/javascript">
    import Auth from "../assets/store/auth";
    import { get, post } from "../assets/helpers/api";
    import { faStar, faMobile, faSquare } from '@fortawesome/free-solid-svg-icons';
    import { faCaretSquareRight } from '@fortawesome/free-solid-svg-icons';
    import Cache from "@aws-amplify/cache";
    var today = new Date();
    const expiration = new Date(today.getFullYear(), (today.getMonth()), today.getDate() + 1);
    export default {
        data() {
            return {
              additionalInfo : this.$route.params.additionalInfo,
                nameState: null,
                body: '',
                vappdetails : this.$route.params.vappdetails,
                reportconflictfound: false,
                vappnotfound: false,
                reportconflictfoundgreen: false,
                isLoading: false,
                online : true,
                disableReportBtn : false,
            }
        },
        created() {
            this.disableReportBtn = this.$route.params.disableReport
            if(navigator.onLine){
                this.online = true
            }else{
                this.online = false
            }
        },

        methods: {
            logout() {
                $("[data-dismiss=modal]").trigger({ type: "click" });
                Auth.set("", "", "");
                //this.$refs['logoutmodal'].modal('hide');
                $("[data-dismiss=modal]").trigger({ type: "click" });
                get("/api/logout")
                    .then(response => {
                        if (response.data.logged_out) {
                            Auth.set("", "", "");
                            Auth.remove();
                            this.$router.push({ path: "/login" });
                        }
                    })
                    .catch(err => {
                        Auth.set("", "", "");
                        Auth.remove();
                        this.$router.push({ path: "/login" });
                    });
            },
            resetModal() {
                this.body = ''
                this.nameState = null
            },
            handleOk(bvModalEvt) {
                // Prevent modal from closing
                bvModalEvt.preventDefault()
                // Trigger submit handler
                this.handleSubmit()
            },
            handleSubmit() {

                var vapp_id = this.vappdetails[0].id;

                let backgroundConflictList = JSON.parse(localStorage.getItem('backgroundConflictTasks'))
                let objIndex = false
                    if(backgroundConflictList) {
                        backgroundConflictList.forEach((item, key) => {
                            if(item != null){
                              if( item.vapp_id == vapp_id){
                                  objIndex = true
                              }
                            }
                        })
                    }
                  if(objIndex){
                          this.$refs['confirm-modal'].show()
                    }else{
                          this.submitForce()
                    }
            },
          goToEvents(){
              this.$router.push({name: "Events"})
          },
          hideConfirmModal(){
                        this.$refs['confirm-modal'].hide()
                        this.$refs['modal'].hide()
                        setTimeout(() => this.$router.push({ name: 'Search', params: { com_name:this.$route.params.com_name, event_id: this.$route.params.event_id, additionalInfo : this.$route.params.additionalInfo ,event_name : this.$route.params.event_name, compound_id: this.$route.params.compound_id} }), 1000)
          },
          submitForce(){

                var event_id = this.vappdetails[0].event_id;
                var compound_id = this.$route.params.compound_id;
                var vapp_id = this.vappdetails[0].id;
                var team_id = this.vappdetails[0].team_id;
                var user_id = this.vappdetails[0].user_id;

          if(navigator.onLine){
                    get("/api/vapp/reportconflict/"+event_id+"/"+compound_id+"/"+vapp_id+"/"+this.body)
                        .then(response => {
                            //console.log(response.data.ErrorMessage);
                            if(response.data.status == 200){
                                this.reportconflictfoundgreen = true;
                                this.vappinfo = 'Override Reported';
                                this.setConflictCache(event_id,compound_id,vapp_id,this.body)
                            }else{
                                this.setConflictCache(event_id,compound_id,vapp_id,this.body)
                                this.reportconflictfoundgreen = true;
                                this.vappinfo = 'Override Reported';
                            }
                            this.isProcessing = false;
                            this.$refs['confirm-modal'].hide()
                            this.$refs['modal'].hide();
                            setTimeout(() => this.$router.push({ name: 'Search', params: { com_name:this.$route.params.com_name, event_id: this.$route.params.event_id, additionalInfo : this.$route.params.additionalInfo ,event_name : this.$route.params.event_name, compound_id: this.$route.params.compound_id} }), 1000)
                        })
                        .catch(err => {
                            if (err.status === 422 && err.response) {
                                this.error = err.response.data.ErrorMessage;
                                if(err.response.data.status == 422){
                                    this.vappnotfound = true;
                                    this.vappinfo = 'Override Reported';
                                    this.setConflictCache(event_id,compound_id,vapp_id,this.body)
                                }
                                this.flashMessage.error({
                                    title: "Error",
                                    message: err.response.data.error,
                                    time: 5000,
                                    flashMessageStyle: {
                                        backgroundColor: "#43d787"
                                    }
                                });
                            }
                            this.isProcessing = false;
                            this.$refs['confirm-modal'].hide()
                            this.$refs['modal'].hide();

                            setTimeout(() => this.$router.push({ name: 'Search', params: { com_name:this.$route.params.com_name, event_id: this.$route.params.event_id, additionalInfo : this.$route.params.additionalInfo ,event_name : this.$route.params.event_name, compound_id: this.$route.params.compound_id} }), 1000)
                        })
                }else{
                    let backgroundConflictList = JSON.parse(localStorage.getItem('backgroundConflictTasks'))
                    if(backgroundConflictList) {
                        let objIndex = false
                        backgroundConflictList.forEach((item, key) => {
                            if(item != null){
                              if( item.event_id == event_id && item.compound_id == compound_id && item.vapp_id == vapp_id){
                                  objIndex = true
                              }
                            }
                        })
                        if(objIndex){
                            this.reportconflictfoundgreen = true;
                            this.vappinfo = "Override Reported"
                            this.setConflictCache(event_id,compound_id,vapp_id,this.body)
                        }else{
                            this.setConflictCache(event_id,compound_id,vapp_id,this.body)
                            this.reportconflictfoundgreen = true;
                            this.vappinfo = "Override Reported";
                        }
                    }else{
                        this.setConflictCache(event_id,compound_id,vapp_id,this.body)
                        this.reportconflictfoundgreen = true;
                        this.vappinfo = "Override Reported";
                    }
                    this.$refs['confirm-modal'].hide()
                    this.$refs['modal'].hide();
                    setTimeout(() => this.$router.push({ name: 'Search', params: { com_name:this.$route.params.com_name, event_id: this.$route.params.event_id, additionalInfo : this.$route.params.additionalInfo ,event_name : this.$route.params.event_name, compound_id: this.$route.params.compound_id} }), 1000)
                    
                }
          },
          setConflictCache(event_id,compound_id,vapp_id,body){
              // Add new vapp to background task
              let vappConflictRecord = {
                  "event_id" : event_id ,
                  "compound_id" : compound_id,
                  "vapp_id" : vapp_id,
                  "body"  : body,
                  "status" : "N"
              }
              let backgroundTasksConflict = JSON.parse(localStorage.getItem('backgroundConflictTasks'))
              if(backgroundTasksConflict){
                  console.log("existed conflict background")
                  backgroundTasksConflict[vapp_id] = vappConflictRecord
              }else {
                  console.log("new conflict background")
                  backgroundTasksConflict = []
                  backgroundTasksConflict[vapp_id] = vappConflictRecord
              }
              localStorage.setItem('backgroundConflictTasks', JSON.stringify(backgroundTasksConflict))
          },
           runBackgroundTasks(){
                if(navigator.onLine) {
                    document.getElementById("syncBtn").innerHTML = "Syncing..."
                    //check if vapps exist in Cache memory
                    if (localStorage.getItem("backgroundTasks")) {
                        let backgroundTasks = JSON.parse(localStorage.getItem("backgroundTasks"));
                        if(backgroundTasks.length > 0){
                            backgroundTasks.forEach((item, key) => {
                                if(item != null){
                                    get("/api/vapp/vapp_compound_status/"+backgroundTasks[key].event_id+"/"+backgroundTasks[key].compound_id+"/"+backgroundTasks[key].vapp_id+"/"+backgroundTasks[key].body+"/"+backgroundTasks[key].vapp_compound_status)
                                        .then(response => {
                                            if(response.data.status == 200 || response.data.status == 422){
                                                backgroundTasks[key] = null
                                                localStorage.setItem('backgroundTasks', JSON.stringify(backgroundTasks))
                                            }
                                        })
                                        .catch(err => {

                                        });
                                }
                            })
                        }
                    }
                    setTimeout(
                        function() {

                        }.bind(this),
                        1000
                    );
                    if (localStorage.getItem("backgroundConflictTasks")) {
                        let backgroundTasks = JSON.parse(localStorage.getItem("backgroundConflictTasks"));
                        if(backgroundTasks.length > 0){
                            backgroundTasks.forEach((item, key) => {
                                if(item != null && backgroundTasks[key].status == 'N'){
                                    get("/api/vapp/reportconflict/"+backgroundTasks[key].event_id+"/"+backgroundTasks[key].compound_id+"/"+backgroundTasks[key].vapp_id+"/"+backgroundTasks[key].body)
                                        .then(response => {
                                            if(response.data.status == 200 || response.data.status == 422){
                                                backgroundTasks[key].status = 'Y'
                                                localStorage.setItem('backgroundConflictTasks', JSON.stringify(backgroundTasks))
                                            }
                                        })
                                        .catch(err => {

                                        });
                                }
                            })
                        }
                    }
                    setTimeout(
                        function() {

                        }.bind(this),
                        1000
                    );
                    //  cache all vapps details
                    get(`/api/vapp/vappalldetails`)
                        .then(response => {
                            localStorage.setItem('vappalldetails', JSON.stringify(response.data))
                        })
                        .catch(err => {
                            this.flashMessage.error({
                                title: "Error",
                                message: "Something went wront please try again later",
                                time: 5000,
                                flashMessageStyle: {
                                    backgroundColor: "#C91F40"
                                }
                            });
                        });

                    setTimeout(
                        function() {
                            document.getElementById("syncBtn").innerHTML = "Synced"
                        }.bind(this),
                        1000
                    );

                    if (localStorage.getItem("events")) {
                        let events = JSON.parse(localStorage.getItem('events'))
                        for (let i= 0; i < events.length ; i ++){
                            get(`/api/vapp/getcompoundsofevent/`+events[i].id)
                                .then(response => {
                                    let compounds = response.data
                                    let compoundNumber = 'comp#'+events[i].id
                                    localStorage.setItem(compoundNumber, JSON.stringify(response.data));

                                        for (let j = 0 ; j < compounds.length ;j++){
                                            get(`/api/vapp/cache/`+events[i].id+"/"+compounds[j].id)
                                                .then(responseCompounds => {
                                                    //this.vapps = response.data;
                                                    //Cache.setItem('vapps', response.data, { expires: expiration.getTime() });
                                                    let vapNumber = 'vapps#'+events[i].id+"=>com"+compounds[j].id
                                                    localStorage.setItem(vapNumber, JSON.stringify(responseCompounds.data));
                                                })
                                                .catch(err => {

                                                })
                                        }
                                })
                                .catch(err => {
                                })
                        }
                    }
                }
            },
        }
    };
</script>

<style scoped>
  html,
  body {
    overflow: hidden;
    position: relative;
    height: 100%;
  }

  .btn-light{
    background-color: #eeeeee !important;
    color: #4a6ee0;
  }
  .btn-light:active{
    background-color: #d7d7d7 !important;
  }
</style>
