<template >
  <div class="wrapper">
    <div class="content-container">
        <FlashMessage position="right top" class="flashmessage"></FlashMessage>

                  <b-modal
                      id="modal-prevent-closing"
                      ref="modal"
                      @show="resetModal"
                      @hidden="resetModal"
                      @ok="handleOk"
                    >
                    <h5 class="modal-title" id="confirmModalLabel">Confirm Override</h5>
                      <form ref="form" @submit.stop.prevent="handleSubmit">
                        <b-form-group
                          :state="nameState"
                          label="Override has been saved. Please provide a brief explanation of the conflict, and the action taken"
                          label-for="body-input"
                          invalid-feedback="body is required"
                        >
                            <b-form-textarea
                            id="textarea-rows"
                            v-model="body"
                            :state="nameState"
                            required
                            class="mt-3"
                            rows="8"
                            ></b-form-textarea>
                        </b-form-group>
                        <div class="form-group">
                          <div class="row justify-content-center">
                            <div class="col-11">
                              <b-button type="submit" variant="primary" block>SAVE AND CONTINUE</b-button>
                            </div>
                          </div>
                        </div>
                      </form>
                    </b-modal>

                    <div v-if="searchvapp == true" class="container-fluid">
                      <div class="row my-3 justify-content-center">
                        <div class="col-11 px-0 py-4">
                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                              Event Name:
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                            {{ vappdetails[0] != null ?  vappdetails[0].event_code : '' }}
                            </div>
                          </div>
                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                              Compound Info:
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                            {{ vappdetails[0] != null ? vappdetails[0].vapp_sn : '' }}
                            </div>
                          </div>
                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                                Status:
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                              {{ vappdetails[0] != null ? vappdetails[0].id : '' }}
                            </div>
                          </div>
                          <div class="row mb-4">
                            <div class="col-12"><hr></div>
                          </div>
                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                              Valid from:
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                            {{ vappdetails[0] != null ? vappdetails[0].valid_from : '' }}
                            </div>
                          </div>

                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                                Valid to:
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                              {{ vappdetails[0] != null ? vappdetails[0].valid_to : '' }}
                            </div>
                          </div>

                          <div class="row mb-4">
                            <div class="col-12"><hr></div>
                          </div>
                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                                Driver Name(s):
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                              {{ vappdetails[0] != null ? vappdetails[0].driver_name : '' }}
                            </div>
                          </div>
                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                                Vehicle Info:
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                              {{ vappdetails[0] != null ? vappdetails[0].original_replacement : ''}}
                            </div>
                          </div>
                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                                Reg Number:
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                              {{ vappdetails[0] != null ? vappdetails[0].vehicle_index_number : '' }}
                            </div>
                          </div>
                          <div class="row row-data">
                            <div class="col-6">
                              <label><strong>
                                VAPP Code:
                              </strong></label>
                            </div>
                            <div class="col-6 text-right">
                              {{ vappdetails[0] != null ? vappdetails[0].vapp_sn : '' }}
                            </div>
                          </div>
                          <div class="row mb-4">
                            <div class="col-12"><hr></div>
                          </div>
                          <b-row class="justify-content-center" v-if="vappdetails[0] != null">
                            <b-col class="col-10 mb-4" v-if="vappdetails[0].vapp_compound_status == 1 && vappdetails[0].vapp_compound_id == $route.params.compound_id"><b-button block variant="primary" @click="vapp_compound_status" id="exitBtn">Confirm Exit</b-button></b-col>
                            <b-col class="col-10 mb-4" v-else-if="vappdetails[0].vapp_compound_status == 0 || vappdetails[0].vapp_compound_status == null"><b-button block variant="primary" @click="vapp_compound_status" id="entryBtn">Confirm Entry</b-button></b-col>
<!--                            <b-col class="col-10"><b-button class="btn btn-block btn-light" v-b-modal.modal-prevent-closing>Report Conflict</b-button></b-col>-->
                            <b-col class="col-10"><b-button class="btn btn-block btn-light" v-on:click="goToInfo">Report Conflict</b-button></b-col>
                          </b-row>

                          <div class="row mt-3" id="vappEnterView">
                            <div v-if="vappEnter === true" style="width: 100%;text-align: center;">
                              <b-alert show variant="success">
                                <h6 class="alert-heading">{{ vappinfo }}</h6>
                              </b-alert>
                            </div>
                          </div>


                        </div>
                      </div>
                    </div>

                  <div v-if="searchvapp == false" class="flex-grow-1 justify-content-center d-flex flex-column h-100">
                    <div class="container-fluid">
                      <div class="row justify-content-center">
                        <div class="row topEventName mt-n5">
                          {{this.event_name}}
                        </div>
                            <div class="col-11 mb-5 t-center">
                              <p>{{this.com_name}}</p>
                              </div>
                        <div class="col-11">
                          <div class="form-group mb-4">
                            <label for="vapp search">Registration/Vapp Number
                            </label>
                            <autocomplete
                              :search="search"
                              ref="vappsearch"
                              placeholder=""
                              aria-label="Registration/Vapp Number"
                              v-on:keyup="keymonitor">
                            </autocomplete>
                          </div>

                          <div class="form-group mb-4">
                            <label for="vapp name phone search">Name/Phone Number</label>
                            <autocomplete
                              :search="searchnamephone"
                              ref="vappnamephone"
                              placeholder=""
                              aria-label="Name/Phone Number"
                              v-on:keyup="keymonitor">
                            </autocomplete>
                          </div>
                          <div class="form-group mb-4">
                              <div class="row justify-content-center">
                                <div class="col-10">
                           <b-button id="btnSearchForVapp" block variant="primary" @click="getvapp">Search</b-button>
                          </div>
                        </div>
                      </div>
                  </div>
            </div>

                      <div v-if="vappnotfound === true">
                        <b-alert show variant="danger">
                          <h4 class="alert-heading">{{ vappinfo }}</h4>
                          <h6>
                            {{ vappsearchdetail }}
                          </h6>
                        </b-alert>
                      </div>

                      <div v-if="reportconflictfound === true">
                        <b-alert show variant="danger">
                          <h6>
                            {{ vappinfo }}
                          </h6>
                        </b-alert>
                      </div>

          </div>
      </div>

      <!-- Bottom Navigation -->
      <ul class="nav nav-bottom fixed-bottom nav-pills nav-justified">
        <li class="nav-item">
          <a class="nav-link" href="#" v-on:click="goToEvents">
            <i class="fas fa-search icon"></i>
            <span class="text">Search</span>
          </a>
        </li>
        <li class="nav-item">
          <span class="nav-link disabled" id="infoMenu" @click="goToInfo">
            <i class="fas fa-info-circle icon"></i>
          <span class="text">Info</span>
          </span>
        </li>
        <li class="nav-item">
          <a class="nav-link" data-toggle="modal" data-target="#logoutModal" href="#logoutModal">
            <i class="fas fa-sign-out-alt icon"></i>
          <span class="text">Logout</span>
          </a>
        </li>
      </ul>
      <!-- /Bottom Navigation -->

    </div>
    <!-- Modal Logout -->
    <div class="modal fade" id="logoutModal" tabindex="-1" role="dialog" aria-labelledby="logoutModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-body">
            <h5 class="modal-title" id="logoutModalLabel">Log Out</h5>
            <p>You are logging out of VAPP.</p>
            <div class="form">
              <div class="form-group">
                <div class="row justify-content-center">
                  <div class="col-11">
                    <button type="button" class="btn btn-block btn-primary" @click.stop="logout" data-dismiss="modal">Log Out</button>
                  </div>
                </div>
              </div>
              <div class="form-group">
                <div class="row justify-content-center">
                  <div class="col-11">
                    <button type="button" class="btn btn-block btn-secondary" data-dismiss="modal">Cancel</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- /Modal Logout -->
  </div>
</template>
<script type="text/javascript">
import Auth from "../assets/store/auth";
import { get, post } from "../assets/helpers/api";
import { faStar, faMobile, faSquare } from '@fortawesome/free-solid-svg-icons';
//import Cache from "@aws-amplify/cache";
var today = new Date();
const expiration = new Date(today.getFullYear(), (today.getMonth()), today.getDate() + 1);
export default {
  data() {
    return {
      form: {
        email: "",
        password: ""
      },
      reportconflictfound: false,
      vappnotfound: false,
      isLoading: false,
      fullPage: true,
      searchvapp: false,
      myIcon: faStar,
      myIcon1: faMobile,
      myIcon2: faSquare,
      error: {},
      name: "",
      body: '',
      nameState: null,
      submittedNames: [],
      vappsearchdetail: "This VAPP has not been found. Search again, or contact base to report a conflict",
      vappinfo: "",
      vapps: [],
      vals: "",
      vappdetails: [],
      vehicle_index_number: [],
      driver_name: [],
      driver_phone: [],
      isProcessing: false,
      event_name : "",
      com_name : "",
      vappEnter : false

    };
  },
  created() {
    //console.log(this.getDateFormat(''))

    this.event_name = this.$route.params.event_name
    this.com_name = this.$route.params.com_name
    this.isProcessing = true;
    this.error = {};
      if (Auth.state.api_token && Auth.state.user_id) {
          if(navigator.onLine){
              get(`/api/vapp/cache/`+this.$route.params.event_id+"/"+this.$route.params.compound_id)
                  .then(response => {
                      this.vapps = response.data;
                      //Cache.setItem('vapps', response.data, { expires: expiration.getTime() });
                      let vapNumber = 'vapps#'+this.$route.params.event_id+"=>com"+this.$route.params.compound_id
                      localStorage.setItem(vapNumber, JSON.stringify(response.data));
                      this.vapps.forEach((item, key) => {
                          this.vehicle_index_number.push(item.vehicle_index_number);
                          this.vehicle_index_number.push(item.vapp_sn);
                          this.driver_name.push(item.driver_name);
                          this.driver_name.push(item.driver_phone);
                      });
                      console.log(this.vapps);
                  })
                  .catch(err => {
                      this.flashMessage.error({
                          title: "Error",
                          message: "Something went wront please try again later",
                          time: 5000,
                          flashMessageStyle: {
                              backgroundColor: "#C91F40"
                          }
                      });
                      this.$router.push({ path: "/search/"+this.$route.params.event_id });
                  });
              this.isProcessing = false;

              //check if vapps exist in Cache memory
              this.runBackgroundTasks()
          }else{
              let vapNumber = 'vapps#'+this.$route.params.event_id+"=>com"+this.$route.params.compound_id
              if (localStorage.getItem(vapNumber)) {
                  this.vapps = JSON.parse(localStorage.getItem(vapNumber));
                  this.vapps.forEach((item, key) => {
                      this.vehicle_index_number.push(item.vehicle_index_number);
                      this.vehicle_index_number.push(item.vapp_sn);
                      this.driver_name.push(item.driver_name);
                      this.driver_name.push(item.driver_phone);
                  });
                  console.log(this.vapps);
              }
          }

      } else {
        console.log("sorry here");
        // Auth.set("", "");
        this.$router.push({ path: "/login" });
      }
    //}

  },

  methods: {
    search(input) {
      if (input.length < 1) { return [] }
      return this.vehicle_index_number.filter(vin => {
        return vin.toLowerCase()
          .startsWith(input.toLowerCase())
      })
    },
    searchnamephone(din) {
      if (din.length < 1) { return [] }
      return this.driver_name.filter(dn => {
        return dn.toLowerCase()
          .includes(din.toLowerCase())
      })
    },
      vapp_compound_status(){
      var event_id = this.vappdetails[0].event_id;
      var compound_id = this.$route.params.compound_id;
      var vapp_id = this.vappdetails[0].id;
      var team_id = this.vappdetails[0].team_id;
      var user_id = this.vappdetails[0].user_id;
      var vapp_compound_status = this.vappdetails[0].vapp_compound_status;
      if(vapp_compound_status == null || vapp_compound_status == 0){
        vapp_compound_status = 0;
      }else{
        vapp_compound_status = 1;
      }
      var body = "The VAPP has been entered into the compound having Event Code = "+this.vappdetails[0].event_code+" and vehicle index number = "+this.vappdetails[0].vehicle_index_number+ " If you have any issues please contact the Administrator";

          if(navigator.onLine){
              get("/api/vapp/vapp_compound_status/"+event_id+"/"+compound_id+"/"+vapp_id+"/"+body+"/"+vapp_compound_status)
                  .then(response => {
                      //console.log(response.data.ErrorMessage);
                      if(response.data.status == 200){
                          this.vappEnter = true;
                          this.vappinfo = response.data.SuccessMessage;
                          this.getAllVappsDetails()
                          document.getElementById("exitBtn").style.backgroundColor = '#28A745'
                          document.getElementById("exitBtn").innerHTML = 'CONFIRMED'
                      }else{
                          this.vappEnter = true;
                          this.vappinfo = response.data.SuccessMessage;
                          let elmnt = document.getElementById("vappEnterView")
                          elmnt.scrollIntoView()
                          document.getElementById("entryBtn").style.backgroundColor = '#78C38B'
                          document.getElementById("entryBtn").innerHTML = 'CONFIRMED'
                      }
                      this.isProcessing = false;
                      //check if vapps exist in Cache memory
                      this.runBackgroundTasks()
                      
                      setTimeout(() => this.searchvapp = false, 1000)
                      setTimeout(() => this.vappEnter = false, 1000)
                  })
                  .catch(err => {
                      if (err.status === 422 && err.response) {
                          this.error = err.response.data.SuccessMessage;
                          if(err.response.data.status == 422){
                              this.vappEnter = true;
                              this.vappinfo = response.data.SuccessMessage;
                              let elmnt = document.getElementById("vappEnterView")
                              elmnt.scrollIntoView()
                              this.getAllVappsDetails()
                              document.getElementById("entryBtn").style.backgroundColor = '#78C38B'
                              document.getElementById("entryBtn").innerHTML = 'CONFIRMED'

                              //check if vapps exist in Cache memory
                              this.runBackgroundTasks()
                          }
                          this.flashMessage.error({
                              title: "Error",
                              message: err.response.data.error,
                              time: 5000,
                              flashMessageStyle: {
                                  backgroundColor: "#43d787"
                              }
                          });
                      }
                      this.isProcessing = false;
                      setTimeout(() => this.searchvapp = false, 1000)
                      setTimeout(() => this.vappEnter = false, 1000)

                  });
          }else{
              if (localStorage.getItem('vappalldetails')) {
                  let vappsList = JSON.parse(localStorage.getItem('vappalldetails'))
                  var resultForEnterExit = vappsList.filter(function(v, i) {
                      return (v["event_id"] == event_id && v["id"] == vapp_id && v["team_id"] == team_id && v["user_id"] == user_id)
                  })
                  if(resultForEnterExit){
                      var objIndex = vappsList.findIndex((obj => obj.event_id == event_id && obj.id == vapp_id && obj.team_id == team_id && obj.user_id == user_id));
                      if(vapp_compound_status == 1){
                          vappsList[objIndex].vapp_compound_status = 0
                          vappsList[objIndex].vapp_compound_id = compound_id
                          vappsList[objIndex].compound_id = compound_id
                          this.vappEnter = true;
                          this.vappinfo = "Your VAPP has been exited successfully from the Compound.";
                          document.getElementById("exitBtn").style.backgroundColor = '#28A745'
                          document.getElementById("exitBtn").innerHTML = 'CONFIRMED'
                      }else{
                          console.log("offline exit")
                          this.vappEnter = true;
                          this.vappinfo = "Your VAPP has been entered successfully in the Compound.";
                          vappsList[objIndex].vapp_compound_status = 1
                          vappsList[objIndex].vapp_compound_id = compound_id
                          vappsList[objIndex].compound_id = compound_id
                          document.getElementById("entryBtn").style.backgroundColor = '#78C38B'
                          document.getElementById("entryBtn").innerHTML = 'CONFIRMED'
                      }
                      localStorage.setItem('vappalldetails', JSON.stringify(vappsList))
                  }else{
                      this.flashMessage.error({
                          title: "Error",
                          message: "Please try again",
                          time: 5000,
                          flashMessageStyle: {
                              backgroundColor: "#43d787"
                          }
                      });
                  }

                  // Add new vapp to background task
                  let vappRecord = {
                      "event_id" : event_id ,
                      "compound_id" : compound_id,
                      "vapp_compound_id" : compound_id,
                      "vapp_id" : vapp_id,
                      "team_id" : team_id,
                      "user_id": user_id,
                      "body"  : body,
                      "vapp_compound_status" : vapp_compound_status
                  }
                  let backgroundTasks = JSON.parse(localStorage.getItem('backgroundTasks'))
                  if(backgroundTasks){
                      console.log("existed background")
                      if(backgroundTasks[vapp_id] != null){
                          backgroundTasks[vapp_id].vapp_compound_status = vapp_compound_status
                      }else{
                          backgroundTasks[vapp_id] = vappRecord
                      }
                  }else {
                      console.log("new background")
                      backgroundTasks = []
                      backgroundTasks[vapp_id] = vappRecord
                  }

                  localStorage.setItem('backgroundTasks', JSON.stringify(backgroundTasks))
                  //  Add new vapp to background task

                      setTimeout(() => this.searchvapp = false, 1000)
                      setTimeout(() => this.vappEnter = false, 1000)
              }
          }

    },
    getvapp() {
      let cid = this.$route.params.compound_id
      let eid = this.$route.params.event_id
      this.isProcessing = true;
      this.error = {};
      if(this.$refs.vappsearch.value.length > 0 && this.$refs.vappnamephone.value.length > 0){
        this.vappnotfound = true;
        this.vappinfo = "";
        this.vappsearchdetail = "Please enter either Vehicle Index Number or Driver Name/Phone Number";
      }else if(this.$refs.vappsearch.value.length < 1 && this.$refs.vappnamephone.value.length < 1){
        this.vappnotfound = true;
        this.vappinfo = "";
        this.vappsearchdetail = "Please provide a Registration or VAPP Number";
      }else if(this.$refs.vappsearch.value > ''){
          let searchString  = this.$refs.vappsearch.value
          document.getElementById("infoMenu").classList.remove("disabled")
          if(navigator.onLine){
              //check if vapps exist in Cache memory
              this.runBackgroundTasks()

              get("/api/vapp/vappdetails/"+this.$refs.vappsearch.value+"/"+eid+"/"+cid)
                  .then(response => {
                      this.vappdetails = response.data;
                      this.searchvapp = true;
                      if(response.data.code == 422){
                          this.searchvapp = false;
                          this.vappnotfound = true;
                          this.vappinfo = this.$refs.vappsearch.value;
                      }else{
                          this.vappnotfound = false;
                          this.vappinfo = "";
                      }
                      this.isProcessing = false;
                  })
                  .catch(err => {
                      if (err.code === 422 && err.response) {
                          this.error = err.response.data.error;
                          if(err.response.data.error.code == 422){
                              this.vappnotfound = true;
                              this.vappinfo = this.$refs.vappsearch.value;
                          }
                          this.flashMessage.error({
                              title: "Error",
                              message: err.response.data.error,
                              time: 5000,
                              flashMessageStyle: {
                                  backgroundColor: "#43d787"
                              }
                          });
                      }
                      this.isProcessing = false;
                  });
          }else{
              if (localStorage.getItem('vappalldetails')) {
                  let vappsList = JSON.parse(localStorage.getItem('vappalldetails'))
                  var result = vappsList.filter(function(v, i) {
                      return (v["event_id"] == eid && (v["vehicle_index_number"] == searchString || v["vapp_sn"] == searchString || v["driver_name"] == searchString || v["driver_phone"] == searchString))
                  })

                  if(result.length > 0){
                      this.vappdetails = result
                      this.searchvapp = true;
                      this.vappnotfound = false;
                      this.vappinfo = "";
                  }else{
                      this.searchvapp = false;
                      this.vappinfo = 'No Matching VAPP found';
                      this.vappdetails = ''
                      this.vappnotfound = true;
                  }
              }
          }

      }else if(this.$refs.vappnamephone.value > ''){
          let searchString  = this.$refs.vappnamephone.value
          document.getElementById("infoMenu").classList.remove("disabled")
          if(navigator.onLine){

              //check if vapps exist in Cache memory
              this.runBackgroundTasks()

              get("/api/vapp/vappdetails/"+this.$refs.vappnamephone.value+"/"+eid+"/"+cid)
                  .then(response => {
                      this.vappdetails = response.data;
                      this.searchvapp = true;
                      if(response.data.code == 422){
                          this.searchvapp = false;
                          this.vappnotfound = true;
                          this.vappinfo = this.$refs.vappnamephone.value;
                      }else{
                          this.vappnotfound = false;
                          this.vappinfo = "";
                      }
                      this.isProcessing = false;
                  })
                  .catch(err => {
                      if (err.code === 422 && err.response.data) {
                          this.error = err.response.data.error;
                          if(err.response.data.error.code == 422){
                              this.vappnotfound = true;
                              this.vappinfo = this.$refs.vappnamephone.value;
                          }
                          this.flashMessage.error({
                              title: "Error",
                              message: err.response.data.error,
                              time: 5000,
                              flashMessageStyle: {
                                  backgroundColor: "#43d787"
                              }
                          });
                      }
                      this.isProcessing = false;
                  });
          }else{
              if (localStorage.getItem('vappalldetails')) {
                  let vappsList = JSON.parse(localStorage.getItem('vappalldetails'))
                  var result = vappsList.filter(function(v, i) {
                      return (v["event_id"] == eid && (v["vehicle_index_number"] == searchString || v["vapp_sn"] == searchString || v["driver_name"] == searchString || v["driver_phone"] == searchString))
                  })

                  if(result.length > 0){
                      this.vappdetails = result
                      this.searchvapp = true;
                      this.vappnotfound = false;
                      this.vappinfo = "";
                  }else{
                      this.searchvapp = false;
                      this.vappinfo = 'No Matching VAPP found';
                      this.vappdetails = ''
                      this.vappnotfound = true;
                  }
              }
          }

      }
    },
    checkFormValidity() {
        const valid = this.$refs.form.checkValidity()
        this.nameState = valid ? 'valid' : 'invalid'
        return valid
      },
      resetModal() {
        this.body = ''
        this.nameState = null
      },
      handleOk(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.handleSubmit()
      },
      handleSubmit() {

      var event_id = this.vappdetails[0].event_id;
      var compound_id = this.$route.params.compound_id;
      var vapp_id = this.vappdetails[0].id;
      var team_id = this.vappdetails[0].team_id;
      var user_id = this.vappdetails[0].user_id;


      get("/api/vapp/reportconflict/"+event_id+"/"+compound_id+"/"+vapp_id+"/"+this.body)
            .then(response => {
              //console.log(response.data.ErrorMessage);
              if(response.data.status == 200){
                this.reportconflictfound = true;
                this.vappinfo = response.data.SuccessMessage;
              }else{
                this.reportconflictfound = true;
                this.vappinfo = response.data.ErrorMessage;
              }
              this.isProcessing = false;
              this.$refs['modal'].hide();
            })
            .catch(err => {
              if (err.status === 422 && err.response) {
                this.error = err.response.data.ErrorMessage;
              if(err.response.data.status == 422){
                this.vappnotfound = true;
                this.vappinfo = response.data.ErrorMessage;
              }
                this.flashMessage.error({
                  title: "Error",
                  message: err.response.data.error,
                  time: 5000,
                  flashMessageStyle: {
                    backgroundColor: "#43d787"
                  }
                });
              }
              this.isProcessing = false;
              this.$refs['modal'].hide();
            });
      },
      logout() {
      $("[data-dismiss=modal]").trigger({ type: "click" });
      Auth.set("", "", "");
      get("/api/logout")
        .then(response => {
          if (response.data.logged_out) {
            Auth.set("", "", "");
            Auth.remove();
            this.$router.push({ path: "/login" });
          }
        })
        .catch(err => {
          Auth.set("", "", "");
          Auth.remove();
          this.$router.push({ path: "/login" });
        });
    },
     goToInfo(){
         this.$router.push({ name: "info" , params: { com_name : this.com_name,event_id : this.$route.params.event_id,event_name : this.$route.params.event_name, additionalInfo : this.$route.params.additionalInfo,  vappdetails: this.vappdetails, compound_id: this.$route.params.compound_id, disableReport : false } })
     },
      goToEvents(){
        this.$router.push({name: "Events"})
      },
      getDateFormat(date){
          if(date != null){
          var tempDate = new Date(date)
          let current_datetime = new Date(tempDate)
          return this.formatAMPM(current_datetime) + " " +  current_datetime.getMonth() + "/" +current_datetime.getDate()  + "/" + current_datetime.getFullYear()
          }
      },
      formatAMPM(date) {
          var hours = date.getHours();
          var minutes = date.getMinutes();
          var ampm = hours >= 12 ? 'PM' : 'AM';
          hours = hours % 12;
          hours = hours ? hours : 12; // the hour '0' should be '12'
          minutes = minutes < 10 ? '0'+minutes : minutes;
          var strTime = hours + ':' + minutes + ' ' + ampm;
          return strTime;
      },
      getAllVappsDetails(){
          if(navigator.onLine){
              get(`/api/vapp/vappalldetails`)
                  .then(response => {
                      localStorage.setItem('vappalldetails', JSON.stringify(response.data))
                  })
                  .catch(err => {
                      this.flashMessage.error({
                          title: "Error",
                          message: "Something went wront please try again later",
                          time: 5000,
                          flashMessageStyle: {
                              backgroundColor: "#C91F40"
                          }
                      });
                  });
          }
      },
      runBackgroundTasks(){
          if(navigator.onLine) {
              //check if vapps exist in Cache memory
              if (localStorage.getItem("backgroundTasks")) {
                  let backgroundTasks = JSON.parse(localStorage.getItem("backgroundTasks"));
                  if(backgroundTasks.length > 0){
                      backgroundTasks.forEach((item, key) => {
                          if(item != null){
                              get("/api/vapp/vapp_compound_status/"+backgroundTasks[key].event_id+"/"+backgroundTasks[key].compound_id+"/"+backgroundTasks[key].vapp_id+"/"+backgroundTasks[key].body+"/"+backgroundTasks[key].vapp_compound_status)
                                  .then(response => {
                                      if(response.data.status == 200 || response.data.status == 422){
                                          backgroundTasks[key] = null
                                          localStorage.setItem('backgroundTasks', JSON.stringify(backgroundTasks))
                                      }
                                  })
                                  .catch(err => {

                                  });
                          }
                      })
                  }
              }

              if (localStorage.getItem("backgroundConflictTasks")) {
                  let backgroundTasks = JSON.parse(localStorage.getItem("backgroundConflictTasks"));
                  if(backgroundTasks.length > 0){
                      backgroundTasks.forEach((item, key) => {
                          if(item != null && backgroundTasks[key].status == 'N'){
                              get("/api/vapp/reportconflict/"+backgroundTasks[key].event_id+"/"+backgroundTasks[key].compound_id+"/"+backgroundTasks[key].vapp_id+"/"+backgroundTasks[key].body)
                                  .then(response => {
                                      if(response.data.status == 200 || response.data.status == 422){
                                          backgroundTasks[key].status = 'Y'
                                          localStorage.setItem('backgroundConflictTasks', JSON.stringify(backgroundTasks))
                                      }
                                  })
                                  .catch(err => {

                                  });
                          }
                      })
                  }
              }

              //  cache all vapps details
              get(`/api/vapp/vappalldetails`)
                  .then(response => {
                      localStorage.setItem('vappalldetails', JSON.stringify(response.data))
                  })
                  .catch(err => {
                      this.flashMessage.error({
                          title: "Error",
                          message: "Something went wront please try again later",
                          time: 5000,
                          flashMessageStyle: {
                              backgroundColor: "#C91F40"
                          }
                      });
                  });

          }
      }
      ,keymonitor: function(event) {
        if(this.search(this.$refs.vappsearch.value).length < 1){
            if(this.searchnamephone(this.$refs.vappnamephone.value).length < 1){
                document.getElementById("btnSearchForVapp").disabled = true;
            }else{
                document.getElementById("btnSearchForVapp").disabled = false;
            }
        }else if(this.searchnamephone(this.$refs.vappnamephone.value).length < 1){
            if(this.search(this.$refs.vappsearch.value).length < 1){
                document.getElementById("btnSearchForVapp").disabled = true;
            }else{
                document.getElementById("btnSearchForVapp").disabled = false;
            }
        }else{
            document.getElementById("btnSearchForVapp").disabled = false;
        }

          this.vappnotfound = false;
          this.reportconflictfound = false;
      }
  }
};
</script>

